h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

a {
  text-decoration: none;
  display: block;
}

img {
  display: block;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: #fff;
  background-color: #141a19;
  margin-inline: auto;
}

button {
  cursor: pointer;
  color: #fff;
  background-color: #394014;
  font-family: Jomolhari;
}

button:disabled {
  cursor: not-allowed;
  fill: #5a5959;
}

button:disabled:hover, button:disabled:focus {
  opacity: 1;
  transform: none;
}

.section {
  padding-top: 80px;
