  height: 60px;
  padding-top: 16px;
  transition: background-color .3s;
  animation: .5s cubic-bezier(.25, .46, .45, .94) 3s both scale-in-ver-center;
  position: fixed;
  inset: 0;
}

.header.scrolled {
  background-color: #141a19e0;
}

.header__wrapper {
