.logo {
  transition: all .3s ease-in;
}

.logo:hover, .logo:focus {
  opacity: .8;
}

.logo__img {
  width: 64px;
  height: 46px;
}

.menu {
  display: none;
}

@media screen and (width >= 835px) {
  .menu {
    gap: 32px;
    display: flex;
  }
}

@media screen and (width >= 1440px) {
