.menu__item {
  letter-spacing: .18px;
  font-family: Lato;
  font-size: 18px;
  font-weight: 700;
  line-height: 140%;
  transition: all .1s ease-in;
}

.menu__item a {
  position: relative;
}

.menu__item a:before {
  content: "";
  transform-origin: 100%;
  background-color: #fff;
  border-radius: 4px;
  width: 100%;
  height: 2px;
  transition: transform .3s ease-in-out;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.menu__item a:hover:before {
  transform-origin: 0;
  transform: scaleX(1);
}

.menu__item:hover, .menu__item:focus {
  opacity: .8;
}

.menu__item a {
  color: #fff;
  text-decoration: none;
}

