}

.socials__link {
  fill: #fff;
}

.socials__link svg {
  fill: inherit;
}

@keyframes scale-in-center {
  0% {
    opacity: 1;
    transform: scale(0);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes puff-in-center {
  0% {
    filter: blur(4px);
    opacity: 0;
    transform: scale(2);
  }

  100% {
    filter: blur();
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes background {
  0% {
    filter: blur(10px);
    opacity: .5;
  }

  100% {
    filter: blur();
    opacity: 1;
  }
}

@keyframes scale-in-ver-center {
  0% {
    opacity: .7;
    transform: scaleY(0);
  }

  100% {
    opacity: 1;
    transform: scaleY(1);
  }
}

